import { Container, Grid, makeStyles, Typography } from '@material-ui/core';
import React from 'react';
import { useAllMembers } from 'src/hooks/members/useAllMembers';
import Emoji from 'components/Utils/Emoji';
import MentorCard from 'components/Cards/MentorCard';

const useStyles = makeStyles(({ spacing, typography }) => ({
  root: {
    padding: `${spacing(15)}px 0`,
  },
  title: {
    fontWeight: typography.fontWeightBold,
    textAlign: 'center',
  },
  description: {
    marginBottom: spacing(10),
    textAlign: 'center',
  },
  grid: {
    width: '20%',
  },
}));

const MentorSection = () => {
  const styles = useStyles();
  const members = useAllMembers();
  const mentors = members.filter(member => ['Evan', 'Daniel', 'Ryan', 'Yurim', 'Youngjin'].includes(member.id));

  return (
    <section className={styles.root}>
      <Container>
        <Typography variant="h4" component="h2" className={styles.title}>
          멘토링 프로젝트를 함께할 멘토들이에요
        </Typography>
        <Typography className={styles.description}>
          멘토들은 각자 1개의 팀을 맡아서 3개월 동안 여러분을 도와줄 거예요. <Emoji emoji="🙌" name="hi" />
          <br />
          프로젝트에 참여하는 모든 멘티는 다른 팀의 멘토나 멘티들과도 자유롭게 소통할 수 있으니 <br />
          멘토의 포지션이 내 포지션과 다르더라도 걱정 마세요.
        </Typography>
        <Grid container>
          {mentors.map(mentor => (
            <Grid item key={mentor.id} xs={12} sm="auto" className={styles.grid}>
              <MentorCard member={mentor} backgroundColor="#FCD1CA" />
            </Grid>
          ))}
        </Grid>
      </Container>
    </section>
  );
};

export default MentorSection;
